export default {
  data() {
    return {
      course_id: "",
      creater: "",
      workTitle: "",
      workMegs: ""
    };
  },
  onLoad(option) {
    let t = this;
    console.log(option);
    
    t.creater = option.creater;
    t.course_id = option.course_id;
  },
  methods: {
    toConfirmWork() {
      let t = this;
      let data = {
        course_id: t.course_id,
        creater: t.creater,
        workTitle: t.workTitle,
        workMegs: t.workMegs
      };
      t.$utils.ajax(t.$api.toAddWorkMegs, "post", data, res => {
        console.log(res);
        t.$utils.showToast(res.msg);
      });
    }
  }
};
